.react-carousel {
  position: relative;
  overflow: hidden;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
}
.react-carousel > .react-carousel-wrapper {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
}
.react-carousel > .react-carousel-wrapper > .react-carousel-wrapper-item {
  height: 100%;
  width: 100%;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
}
.react-carousel > .react-carousel-indicator {
  position: absolute;
  bottom: 30px;
  width: 100%;
  height: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.react-carousel > .react-carousel-indicator > .react-carousel-indicator-item {
  width: 10px;
  height: 10px;
  background: #ccc;
  border-radius: 50%;
  margin: 0 5px;
}
.react-carousel > .react-carousel-indicator > .react-carousel-indicator-item.active {
  background: #f80;
}
